.vehicleIntroduction {
    background-color: #fff;
    text-align: center;
    margin: 12.5vh auto;
    .gsap-animation {
        opacity: 0;
        transform: translateY(125px);
    }
    h2 {
        font-weight: 600;
        font-size: 4.5rem;
        line-height: calc(4.5rem * 1.3);
        letter-spacing: -1px;
        word-spacing: 7.5px;
    }
    p {
        font-size: 1.3rem;
        width: 35%;
        margin: 1.5rem auto 0;
        line-height: calc(1.3rem * 1.35);
        color: rgb(100, 100, 100);
    }
    img {
        object-fit: cover;
        width: 50%;
        user-select: none;
    }
    .vehicle-stats-wrapper {
        display: flex;
        justify-content: space-between;
        width: 60%;
        margin: 0 auto 5vh;
        .vehicle-stats_item {
            width: 33.3333%;
            opacity: 0;
            transform: translateY(50px);
            p {
                color: rgb(100, 100, 100);
                font-size: 15px;
                margin-bottom: 0.3rem;
            }
            h3 {
                font-size: 3.5rem;
                font-weight: 600;
                span {
                    color: rgb(75, 75, 75);
                    font-size: 1.25rem;
                    font-weight: 400;
                }
            }
        }
    }
    button {
        background-color: black;
        color: white;
        font-weight: 500;
        font-size: 15px;
        border: none;
        outline: none;
        text-transform: uppercase;
        display: flex;
        align-items: center;
        padding: 1.5rem 2.75rem;
        transition: .15s ease-in-out;
        margin: 4.5rem auto;
        cursor: pointer;
        .arrow-icon {
            font-size: 1.1rem;
            margin-left: 1rem;
            color: rgb(195, 139, 33);
        }
        &:hover {
            background-color: rgb(30, 30, 30);
        }
    }
    .order-vehicle-container {
        margin: 7.5vh 0;
        video {
            object-fit: cover;
            width: 100%;
            height: 70vh;
            object-position: top;
            filter: brightness(0.5);
        }
        .order-vehicle {
            padding: 6.25rem 0 5rem;
            background: #000;
            color: #fff;
            margin-top: -1vh;
            h2 {
                font-weight: 500;
                font-size: 4.75rem;
                line-height: calc(4.5rem * 1.3);
                letter-spacing: -2px;
            }
            p {
                letter-spacing: 0.25px;
                font-size: 1.4rem;
                line-height: calc(1.3rem * 1.4);
            }
            .order-vehicle-button {
                background-color: #fff;
                color: #000;
                font-weight: 500;
                font-size: 15px;
                border: none;
                outline: none;
                text-transform: uppercase;
                margin: 5rem auto 0;
                display: flex;
                align-items: center;
                cursor: pointer;
                padding: 1.1rem 2rem;
                transition: .15s ease-in-out;
                margin-bottom: 0.75rem;
                .arrow-icon {
                    font-size: 1.5rem;
                    margin-left: 0.75rem;
                }
                &:hover {
                    background-color: rgb(220, 220, 220);
                }
            }
        }
    }
}

@media screen and (max-width: 1450px) {
    .vehicleIntroduction {
        p {
            font-size: 1.3rem;
            width: fit-content;
        }
        .vehicle-stats-wrapper {
            width: 75%;
        }
    }
}

@media screen and (max-width: 1240px) {
    .vehicleIntroduction {
        .vehicle-stats-wrapper {
            width: 90%;
        }
        .order-vehicle-container video {
            height: 60vh;
        }
    }
}

//! iPad Pro
@media screen and (orientation: portrait) and (max-width: 1050px) {
    .vehicleIntroduction {
        margin: 7.5vh auto 10vh;
        h2 {
            font-size: 5rem;
            letter-spacing: 0.25px;
        }
        p {
            font-size: 1.5rem;
            line-height: calc(1.5rem * 1.35);
            width: 65%;
            letter-spacing: 0.25px;
        }
        img {
            width: 55%;
        }
        button {
            font-size: 18px;
            .arrow-icon {
                font-size: 20px;
            }
        }
        .vehicle-stats-wrapper {
            width: 92.5%;
        }
        .order-vehicle-container {
            video {
                height: 35vh;
            }
            .order-vehicle {
                p {
                    font-size: 1.6rem;
                }
                .order-vehicle-button {
                    font-size: 16px;
                }
            }
        }
    }
}

//! iPad Air
@media screen and (orientation: portrait) and (max-width: 840px) {
    .vehicleIntroduction {
        p {
            width: 85%;
        }
        img {
            width: 60%;
        }
        .vehicle-stats-wrapper {
            width: 95%;
            .vehicle-stats_item {
                p {
                    font-size: 1rem;
                }
            }
        }
    }
}

//! iPhone
@media screen and (max-width:430px) {
    .vehicleIntroduction {
        h2 {
            font-size: 4rem;
            line-height: 4rem;
        }
        p {
            font-size: 16px;
            line-height: 23px;
            width: 97.5%;
        }
        img {
            width: 75%;
        }
        .vehicle-stats-wrapper {
            width: 100%;
            .vehicle-stats_item {
                p {
                    font-size: 14px;
                }
                h3 {
                    font-size: 2.1rem;
                    span {
                        font-size: 15px;
                    }
                }
            }
        }
        .order-vehicle-container {
            video {
                height: 30vh;
            }
            .order-vehicle {
                padding: 5rem 0 6rem;
                h2 {
                    font-size: 2.55rem;
                    line-height: 3rem;
                    word-spacing: 1px;
                }
                p {
                    margin-top: 0.5rem;
                    font-size: 1.275rem;
                    width: 98%;
                }
                .order-vehicle-button {
                    font-size: 14px;
                    margin-top: 3rem;
                }
            }
        }
    }
}