.avaOne-content {
    .avaOne-video-wrapper {
        padding-top: 15vh;
        padding-bottom: 10vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #F4F4F2;
        position: relative;
        .avaOne-video {
            object-fit: cover;
            width: 85%;
            height: 75vh;
            border-radius: 55px;
            filter: brightness(0.525);
        }
        .video-text {
            height: 100%;
            text-align: center;
            user-select: none;
            display: flex;
            flex-direction: column;
            z-index: 50;
            top: 92.5%;
            left: 30%;
            transform: translate(-50%, -50%);
            position: absolute;
            color: #fff;
            h1 {
                font-weight: 500;
                font-size: 5.25rem;
                margin-bottom: 1.75rem;
                line-height: calc(5rem * 1.2);
            }
            li {
                text-align: left;
                margin: 0.75rem auto;
                font-size: 1.3rem;
                line-height: calc(1.3rem * 1.35);
                letter-spacing: -0.1px;
            }
        }
    }
    .avaOne-introduction-container {
        width: 87.5%;
        margin: 5vh auto;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .avaOne-introduction-wrapper {
            margin: auto;
            width: 80%;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-around;
            .vehicle-thumbnail {
                width: 40%;
                height: 75vh;
                border-radius: 50px;
                object-fit: cover;
            }
            .vehicle-thumbnail-wide {
                width: 55%;
                height: auto;
                margin-top: -4vh;
                margin-right: 3.75%;
                border-radius: 50px;
            }
            .vehicle-thumbnail_wide {
                margin-top: 7.5vh;
                margin-right: 5%;
                width: 55%;
                height: auto;
                transform: scale(-1, 1);
                border-radius: 50px;
            }
            h3 {
                text-align: center;
                width: 42.5%;
                font-size: 4.7rem;
                font-weight: 500;
            }
            .avaOne-introduction_description {
                p {
                    word-spacing: 1.25px;
                    letter-spacing: 0.1px;
                    width: 90%;
                    font-size: 1.275rem;
                    margin: 1.75rem 0;
                }
                button {
                    background-color: black;
                    color: white;
                    font-weight: 500;
                    border: none;
                    outline: none;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    text-transform: uppercase;
                    padding: 1.1rem 2.5rem;
                    margin: 3.75rem auto 0;
                    margin-bottom: 0.75rem;
                    cursor: pointer;
                    transition: .15s ease-in-out;
                    .arrow-icon {
                        font-size: 1.25rem;
                        margin-left: 0.75rem;
                        color: rgb(195, 139, 33);
                        transform: rotate(90deg);
                    }
                    &:hover {
                        background-color: rgb(30, 30, 30);
                    }
                }
            }
        }
        .wider {
            width: 100%;
        }
    }
}

.avaOne-safety {
    margin: 20vh auto 2.5vh;
    width: 87.5%;
    h2 {
        font-size: 5.1rem;
        font-weight: 400;
        text-align: center;
        margin-bottom: 2.25rem;
    }
    .safety-container {
        display: flex;
        justify-content: space-between;
        .safety-vehicle-image {
            margin: 5% 7.5% 0;
            width: 72.5%;
            height: 50vh;
            object-fit: cover;
            opacity: 0;
            transform: translateY(100px);
        }
        .safety-wrapper-left,
        .safety-wrapper-right {
            text-align: right;
            display: flex;
            flex-direction: column;
            div {
                margin: 2.5rem 0;
                h3 {
                    font-weight: 400;
                    font-size: 3.7rem;
                }
                p {
                    margin-top: 0.5rem;
                    font-size: 1.525rem;
                }
            }
        }
        .safety-wrapper-left {
            text-align: left;
        }
    }
}

.avaOne-techSpecs {
    margin-top: 7.5vh;
    padding: 5vh auto;
    background-color: rgb(20, 20, 20);
    color: #fff;
    h2 {
        padding: 7.5vh 0;
        text-align: center;
        font-weight: 400;
        font-size: 5.75rem;
    }
    .specs-table {
        margin: 0 auto;
        width: 57.5%;
        padding-bottom: 12.5vh;
        .specs-table_row {
            font-size: 1.15rem;
            font-weight: 800;
            padding: 1rem 1.4rem;
            border: 1px solid #fff;
            border-bottom: none;
            display: flex;
            justify-content: space-between;
            &:last-of-type {
                border-bottom: 1px solid #fff;
            }
        }
    }
}

.avaOne-gallery {
    margin: 5vh auto -5rem;
    .gallery-container {
        margin: 5vh auto;
        width: 85%;
        display: flex;
        justify-content: space-between;
        .gallery-image-1 {
            width: 48%;
            border-radius: 50px;
            object-fit: cover;
            height: 80vh;
        }
        .gallery-images-wrapper {
            width: 50%;
            height: 80vh;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;
            .gallery-image-2 {
                width: 100%;
                height: 47.5%;
                object-fit: cover;
                border-radius: 50px;
            }
            .gallery-image-3 {
                width: 42.5%;
                height: 47.5%;
                object-fit: cover;
                border-radius: 50px;
            }
            .gallery-image-4 {
                width: 50%;
                height: 47.5%;
                object-fit: cover;
                border-radius: 40px;
                filter: brightness(0.75);
            }
        }
    }
    .gallery-order {
        text-align: center;
        padding: 8vh 0 9vh;
        color: #fff;
        background-color: rgb(20, 20, 20);
        h2 {
            font-weight: 400;
            font-size: 5rem;
            margin: 1rem 0;
        }
        p {
            margin: 1.75rem 0 2.5rem;
            font-size: 1.5rem;
        }
        button {
            font-size: 16px;
            background-color: #fff;
            color: #000;
            font-weight: 500;
            border: none;
            outline: none;
            display: flex;
            justify-content: center;
            align-items: center;
            text-transform: uppercase;
            padding: 1.1rem 2.4rem;
            margin: 3.75rem auto 0;
            margin-bottom: 0.75rem;
            transition: .175s ease-in-out;
            cursor: pointer;
            .arrow-icon {
                font-size: 1.7rem;
                margin-left: 0.7rem;
                color: rgb(195, 139, 33);
            }
            &:hover {
                background-color: rgb(220, 220, 220);
            }
        }
    }
}

.gsap-animated-heading {
    opacity: 0;
    transform: translateY(100px);
}

.gsap-animated-image,
.gsap-animated-image-2,
.gsap-animated-image-3 {
    opacity: 0;
    transform: translateY(75px);
}

.gsap-animated-paragraph,
.gsap-animated-paragraph-2 {
    opacity: 0;
    transform: translateY(75px);
}

.gsap-animated-wrapper-left {
    opacity: 0;
    transform: translateX(-100px);
}

.gsap-animated-wrapper-right {
    opacity: 0;
    transform: translateX(100px);
}

@media screen and (max-width: 1450px) {
    .avaOne-content {
        .avaOne-video-wrapper {
            .avaOne-video {
                width: 87.5%;
                border-radius: 60px;
            }
            .video-text {
                left: 35%;
            }
        }
        .avaOne-introduction-container .avaOne-introduction-wrapper {
            width: 95%;
        }
    }
    .avaOne-safety {
        width: 90%;
        .safety-container .safety-wrapper-left div p,
        .safety-container .safety-wrapper-right div p {
            font-size: 1.45rem;
        }
        .safety-container {
            .safety-vehicle-image {
                width: 70%;
                height: 47.5vh;
            }
        }
    }
    .avaOne-techSpecs .specs-table {
        width: 65%;
    }
    .avaOne-gallery .gallery-container {
        margin: 7.5vh auto;
    }
}

@media screen and (max-width: 1250px) {
    .avaOne-content {
        .avaOne-video-wrapper {
            .avaOne-video {
                height: 70vh;
                width: 95%;
                border-radius: 55px;
            }
        }
        .avaOne-introduction-container .avaOne-introduction-wrapper {
            width: 100%;
        }
    }
    .avaOne-safety {
        width: 94%;
        overflow-x: hidden;
        .safety-container .safety-wrapper-left div h3,
        .safety-container .safety-wrapper-right div h3 {
            font-size: 2.75rem;
        }
        .safety-container .safety-wrapper-left div p,
        .safety-container .safety-wrapper-right div p {
            font-size: 1.25rem;
        }
        .safety-container {
            .safety-vehicle-image {
                height: 42.5vh;
                margin: 5% 1rem 0;
            }
        }
    }
    .avaOne-techSpecs .specs-table {
        width: 80%;
    }
    .avaOne-gallery .gallery-container {
        width: 97.5%;
        margin: 7.5vh auto;
    }
}

//! iPad Pro
