//! iPhone
@media screen and (max-width:430px) {
    .avaOne-content {
        .avaOne-video-wrapper {
            .avaOne-video {
                height: 37.5vh;
                width: 97.5%;
                border-radius: 35px;
            }
            .video-text {
                height: auto;
                top: 52.5%;
                left: 57.5%;
                width: 100%;
                h1 {
                    text-align: left;
                    font-size: 2.5rem;
                    line-height: 3rem;
                }
                li {
                    font-size: 15px;
                    margin: 0.25rem auto;
                }
            }
        }
        .avaOne-introduction-container {
            .avaOne-introduction-wrapper {
                flex-direction: column-reverse;
                .vehicle-thumbnail {
                    width: 99%;
                    height: 32.5vh;
                    border-radius: 27.5px;
                }
                h3 {
                    margin-bottom: 2.5rem;
                    width: 97.5%;
                    font-size: 3.5rem;
                }
                .avaOne-introduction_description {}
                .vehicle-thumbnail-wide {
                    width: 87.5%;
                    border-radius: 25px;
                    margin-top: -5vh;
                }
                .avaOne-introduction_description p {
                    width: 100%;
                    font-size: 1.2rem;
                }
            }
        }
    }
    .avaOne-content .avaOne-introduction-container .avaOne-introduction-wrapper .vehicle-thumbnail_wide {
        width: 95%;
        border-radius: 25px;
        margin: 7vh auto 0;
    }
    .avaOne-safety {
        margin: 10vh auto 2.5vh;
        h2 {
            font-size: 3rem;
            font-weight: 500;
        }
        .safety-container {
            flex-direction: column;
            .safety-wrapper-left div,
            .safety-wrapper-right div {
                margin: 0.4rem auto;
                text-align: center;
                h3 {
                    display: none;
                }
                p {
                    text-align: justify;
                    font-size: 15px;
                    margin: 0 auto;
                    font-weight: 500;
                }
            }
            .safety-vehicle-image {
                margin: 0 auto;
                height: 27.5vh;
                overflow: visible;
            }
        }
    }
    .avaOne-techSpecs {
        h2 {
            font-size: 3.75rem;
        }
        .specs-table {
            width: 97.5%;
            .specs-table_row {
                font-size: 13px;
                font-weight: 400;
                padding: 1rem 0.4rem;
                letter-spacing: -0.25px;
                .mobile-fix {
                    text-align: right;
                }
            }
        }
    }
    .avaOne-gallery {
        .gallery-container {
            flex-direction: column;
            .gallery-image-1 {
                margin: 0 auto;
                width: 99%;
                height: 37.5vh;
            }
            .gallery-images-wrapper {
                margin: 0.25rem auto;
                width: 99%;
                height: 70vh;
                .gallery-image-2 {
                    height: 48%;
                }
                .gallery-image-3 {
                    width: 49%;
                    height: 49%;
                }
                .gallery-image-4 {
                    width: 49%;
                    height: 49%;
                }
            }
        }
        .gallery-order {
            h2 {
                font-size: 3rem;
            }
            p {
                font-size: 1.25rem;
            }
        }
    }
}