.latest-news {
    background-color: #fff;
    margin: 5vh auto 7.5vh;
    h1 {
        font-size: 4.7rem;
        font-weight: 500;
        text-align: center;
        margin-bottom: 5rem;
    }
    .latest-news_wrapper {
        width: 87.5%;
        margin: 2.5vh auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        display: grid;
        grid-template-columns: repeat(3, minmax(0, 1fr));
        gap: 2.5rem;
        .news-article {
            display: flex;
            justify-content: flex-start;
            height: 570px;
            height: 60vh;
            align-items: flex-start;
            flex-direction: column;
            .news-thumbnail {
                width: 100%;
                height: 330px;
                height: 35vh;
                border-radius: 32px;
                cursor: pointer;
                background-size: cover;
            }
            .news-heading {
                font-size: 2rem;
                font-weight: 500;
                margin: 1.3rem 0 1rem;
                line-height: calc(2rem * 1.2);
                cursor: pointer;
                &:hover {
                    text-decoration: underline;
                }
            }
            .news-description {
                font-size: 18px;
                text-align: justify;
                line-height: calc(18px * 1.4);
                color: rgb(100, 100, 100);
                width: 80%;
            }
        }
    }
    .news-button {
        background-color: #000;
        color: #fff;
        font-weight: 500;
        font-size: 16px;
        border: none;
        outline: none;
        text-transform: uppercase;
        margin: 7.5vh auto 2.5vh;
        display: flex;
        align-items: center;
        cursor: pointer;
        padding: 1.15rem 2.25rem;
        transition: .15s ease-in-out;
        margin-bottom: 0.75rem;
        letter-spacing: 0.25px;
        word-spacing: 2px;
        .arrow-icon {
            color: rgb(195, 139, 33);
            font-size: 1.6rem;
            margin-left: 1rem;
        }
        &:hover {
            background-color: rgb(17, 17, 17);
        }
    }
}

@media screen and (max-width: 1450px) {
    .latest-news {
        .latest-news_wrapper {
            width: 95%;
            .news-article {
                height: 55vh;
                .news-thumbnail {
                    height: 30vh;
                }
                .news-heading {
                    text-align: justify;
                    font-size: 1.8rem;
                }
                .news-description {
                    width: 95%;
                    font-size: 17px;
                    line-height: 24px;
                }
            }
        }
    }
}

@media screen and (max-width: 1240px) {
    .latest-news {
        .latest-news_wrapper {
            width: 97.5%;
            .news-article {
                height: 45vh;
                .news-thumbnail {
                    height: 22.5vh;
                }
                .news-heading {
                    font-size: 1.4rem;
                    font-weight: 600;
                    line-height: 2.2rem;
                    margin: 1rem 0 0.5rem;
                }
                .news-description {
                    width: 95%;
                    font-size: 16px;
                    line-height: 22px;
                }
            }
        }
    }
}

//! iPad Pro
@media screen and (orientation: portrait) and (max-width: 1050px) {
    .latest-news {
        .latest-news_wrapper {
            grid-template-columns: repeat(2, minmax(0, 1fr));
            .news-article {
                height: 30vh;
                .news-thumbnail {
                    height: 20vh;
                }
                .news-heading {
                    font-weight: 700;
                    font-size: 1.5rem;
                    width: 92.5%;
                    margin: 0.75rem auto 0.55rem;
                }
                .news-description {
                    font-size: 18px;
                    width: 92.5%;
                    margin: 0 auto;
                }
            }
        }
    }
}

//! iPad Air
@media screen and (orientation: portrait) and (max-width: 840px) {
    .latest-news .latest-news_wrapper {
        .news-article {
            .news-thumbnail {
                height: 17.5vh;
            }
            .news-heading {
                width: 95%;
                font-size: 1.4rem;
                line-height: 1.9rem;
            }
            .news-description {
                width: 95%;
                line-height: 21px;
            }
        }
    }
}

//! iPhone
@media screen and (max-width:430px) {
    .latest-news h1 {
        font-size: 4rem;
    }
    .latest-news .latest-news_wrapper {
        gap: 1.25rem;
    }
    .latest-news .latest-news_wrapper {
        .news-article {
            height: 35vh;
            .news-thumbnail {
                min-height: 12.5vh;
                height: 12.5vh;
                border-radius: 15px;
            }
            .news-heading {
                text-align: left;
                width: 100%;
                font-size: 1.15rem;
                line-height: 1.6rem;
            }
            .news-description {
                width: 100%;
                line-height: 21px;
                font-size: 16px;
                text-align: left;
            }
        }
    }
}