@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400..900&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Maven Pro", sans-serif;
    &::selection {
        color: rgb(0, 0, 0);
        background-color: rgb(189, 189, 189);
    }
}

a {
    text-decoration: none;
}

img {
    user-select: none;
}