.mainPage {
    padding-top: 12.5vh;
    padding-bottom: 7.5vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #F4F4F2;
    .main-video {
        object-fit: cover;
        width: 82.5%;
        height: 82.5%;
        border-radius: 80px;
        filter: brightness(0.75);
    }
    .video-text {
        width: 100%;
        height: 100%;
        text-align: center;
        user-select: none;
        display: flex;
        flex-direction: column;
        z-index: 50;
        top: 100%;
        left: 50%;
        transform: translate(-50%, -60%);
        position: absolute;
        color: #fff;
        h1 {
            font-weight: 400;
            font-size: 5.25rem;
            margin-bottom: 1.5rem;
            line-height: calc(5.25rem * 1.2);
            letter-spacing: -2.5px;
        }
        p {
            width: 37.5%;
            margin: 0 auto;
            font-size: 1.3rem;
            line-height: calc(1.3rem * 1.4);
            text-wrap: pretty;
        }
        .icon-wrapper {
            margin-top: 10%;
            span {
                display: block;
                text-transform: uppercase;
                font-size: 0.825rem;
            }
            .icon {
                cursor: pointer;
                margin-top: 0.6rem;
                color: orange;
                font-size: 1.75rem;
            }
        }
    }
}

@media screen and (max-width: 1470px) {
    .mainPage {
        padding-top: 15vh;
        .main-video {
            width: 87.5%;
            border-radius: 70px;
        }
        .video-text {
            transform: translate(-50%, -65%);
            h1 {
                font-weight: 500;
                letter-spacing: -2px;
            }
            p {
                width: 50%;
            }
            .icon-wrapper {
                span {
                    font-size: 14px;
                    letter-spacing: 0.25px;
                }
            }
        }
    }
}

@media screen and (max-width: 1240px) {
    .mainPage {
        .main-video {
            width: 90%;
        }
        .video-text {
            transform: translate(-50%, -67.5%);
            h1 {
                font-size: 4.75rem;
            }
            p {
                width: 70%;
                font-size: 1.2rem;
            }
            .icon-wrapper {
                margin-top: 7.5%;
                span {
                    font-size: 15px;
                    letter-spacing: 0.25px;
                }
            }
        }
    }
}

//! iPad Pro
@media screen and (orientation: portrait) and (max-width: 1050px) {
    .mainPage {
        .main-video {
            width: 92.5%;
        }
        .video-text {
            height: auto;
            top: 50%;
            transform: translate(-50%, -100%);
            h1 {
                font-size: 4.5rem;
            }
            p {
                font-size: 19px;
            }
            .icon-wrapper span {
                font-size: 1rem;
                margin-bottom: 0.25rem;
            }
        }
    }
}

//! iPad Air
@media screen and (orientation: portrait) and (max-width: 840px) {
    .mainPage {
        .main-video {
            width: 95%;
            border-radius: 55px;
        }
        .video-text {
            top: 47.5%;
            transform: translate(-50%, -100%);
            h1 {
                font-size: 3.75rem;
            }
            p {
                width: 85%;
                font-size: 18px;
                line-height: calc(18px * 1.4);
            }
            .icon-wrapper span {
                font-size: 14px;
                margin-bottom: 0.25rem;
            }
        }
    }
}

//! iPhone
@media screen and (max-width:430px) {
    .mainPage {
        .main-video {
            height: 37.5vh;
            width: 98.5%;
            border-radius: 30px;
        }
        .video-text {
            top: 52.5%;
            transform: translate(-50%, -100%);
            h1 {
                font-size: 2rem;
                line-height: 2rem;
                margin-bottom: 1rem;
            }
            p {
                width: 95%;
                font-size: 13px;
                line-height: calc(13px * 1.35);
            }
            .icon-wrapper {
                margin-top: 10%;
                span {
                    font-size: 13px;
                    margin-bottom: 0.2rem;
                }
                .icon {
                    font-size: 1.5rem;
                    margin-top: 2px;
                }
            }
        }
    }
}