.team-content {
    div {
        padding: 2.5vh 0 1.25vh;
        background-color: #F4F4F2;
        .team-image {
            display: flex;
            margin: 15vh auto;
            width: 85%;
            height: 70vh;
            object-fit: cover;
            border-radius: 50px;
            filter: brightness(0.35);
            object-position: bottom;
        }
        .team-image_description {
            background-color: transparent;
            color: #fff;
            text-align: center;
            position: absolute;
            top: 55%;
            left: 50%;
            transform: translate(-50%, -50%);
            user-select: none;
            width: 100%;
            h4 {
                font-weight: 400;
                font-size: 5.5rem;
            }
            p {
                margin: 1.5rem 0;
                font-size: 1.4rem;
            }
        }
    }
    .founders {
        background-color: #fff;
        margin: 17.5vh auto 15vh;
        width: 82.5%;
        display: flex;
        justify-content: space-between;
        .gsap-animation {
            opacity: 0;
            transform: translateY(100px);
        }
        aside {
            width: 40%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            h2 {
                font-weight: 400;
                font-size: 4.75rem;
                margin: 2rem 0;
            }
            p {
                margin: 1rem 0;
                font-size: 1.2rem;
                line-height: calc(1.2rem * 1.25);
            }
        }
        .founders-image {
            opacity: 0;
            width: 50%;
            height: 70vh;
            object-fit: cover;
            border-radius: 40px;
        }
    }
}

@media screen and (max-width: 1300px) {
    .team-content {
        div {
            .team-image {
                width: 90%;
                height: 65vh;
                border-radius: 50px;
                filter: brightness(0.35);
            }
            .team-image_description {
                top: 47.5%;
            }
        }
        .founders {
            width: 87.5%;
            aside h2 {
                font-weight: 500;
            }
        }
    }
}

//! iPad Pro
@media screen and (orientation: portrait) and (max-width: 1050px) {
    .team-content {
        div {
            .team-image {
                width: 95%;
                height: 45vh;
                border-radius: 50px;
                filter: brightness(0.35);
            }
            .team-image_description {
                top: 40%;
                h4 {
                    font-size: 5.75rem;
                }
                p {
                    font-size: 1.5rem;
                    width: 50%;
                    margin: 1.5rem auto;
                }
            }
        }
        .founders {
            margin: 12.5vh auto;
            width: 95%;
            flex-direction: column;
            align-items: center;
            aside {
                width: 80%;
                h2 {
                    font-size: 5rem;
                    text-align: center;
                }
                p {
                    font-size: 1.6rem;
                    line-height: calc(1.6rem * 1.35);
                    text-align: justify;
                }
            }
            .founders-image {
                margin-top: 5vh;
                width: 97.5%;
                height: 50vh;
            }
        }
    }
}

//! iPhone
@media screen and (max-width:430px) {
    .team-content {
        div {
            .team-image {
                width: 97.5%;
                height: 42.5vh;
                border-radius: 30px;
            }
            .team-image_description {
                top: 40%;
                h4 {
                    font-size: 3.75rem;
                    font-weight: 500;
                }
                p {
                    font-size: 1.2rem;
                    width: 95%;
                    margin: 1.25rem auto;
                }
            }
        }
        .founders {
            margin: 5vh auto;
            width: 98%;
            aside {
                width: 95%;
                h2 {
                    font-size: 3.25rem;
                }
                p {
                    font-size: 1.2rem;
                    line-height: calc(1.2rem * 1.35);
                }
            }
            .founders-image {
                border-radius: 25px;
                margin-top: 5vh;
                width: 99%;
                height: 37.5vh;
            }
        }
    }
}