.orderVehicle-section {
    margin-top: 20vh;
    article {
        h2 {
            font-size: 5.5rem;
            font-weight: 400;
            letter-spacing: 1px;
            text-align: center;
        }
        p {
            font-size: 1.3rem;
            text-align: center;
            width: 40%;
            text-wrap: balance;
            margin: 1rem auto;
            letter-spacing: 0.25px;
            line-height: calc(1.3rem * 1.3);
            color: rgb(90, 90, 90);
        }
        button {
            font-size: 16px;
            margin: 4rem auto 4.5rem;
            padding: 1.15rem 2.1rem;
            background-color: black;
            color: white;
            font-weight: 400;
            border: none;
            outline: none;
            display: flex;
            justify-content: center;
            align-items: center;
            text-transform: uppercase;
            cursor: pointer;
            transition: 0.14s ease-in-out;
            .arrow-icon {
                transform: rotate(90deg);
                font-size: 1.325rem;
                margin-left: 0.8rem;
                color: rgb(195, 139, 33);
            }
            &:hover {
                background-color: rgb(35, 35, 35);
            }
        }
    }
    .orderVehicle-thumbnail {
        width: 100%;
        height: 62.5vh;
        filter: brightness(0.85);
        object-position: top;
        object-fit: cover;
    }
}

.orderVehicle-form {
    margin: 10vh 0;
    h1 {
        font-size: 5rem;
        font-weight: 400;
        letter-spacing: 1px;
        text-align: center;
    }
    .form-container {
        width: 85%;
        margin: 10vh auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        video {
            border-radius: 32px;
            width: 50%;
            height: auto;
            object-fit: cover;
            filter: brightness(0.7);
        }
        .form-info-wrapper {
            width: 45%;
            margin-left: 10%;
            .form-info {
                margin: 0 0 2.3rem;
                width: 90%;
                h4 {
                    font-size: 1.1rem;
                    font-weight: 600;
                    letter-spacing: -0.2px;
                    margin-bottom: 0.25rem;
                }
                p {
                    color: rgb(98, 98, 96);
                    letter-spacing: 0.1px;
                    display: flex;
                    align-items: center;
                }
                h3 {
                    font-size: 1.55rem;
                    color: rgb(40, 40, 40);
                    font-weight: 400;
                    margin-bottom: 0.25rem;
                }
            }
            .form-info-price {
                display: flex;
                justify-content: space-between;
                align-items: flex-start;
                .icon-info {
                    margin-left: 0.3rem;
                }
                .full_price {
                    color: #000;
                    font-size: 1.05rem;
                    font-weight: 700;
                }
                .todayPay-price {
                    color: rgb(25, 25, 25);
                    font-size: 2rem;
                }
            }
            .final-payment {
                width: 85%;
                border-radius: 0.75rem;
                background-color: rgb(244, 244, 241);
                p {
                    font-size: 0.875rem;
                    line-height: 15.5px;
                    padding: 0.75rem;
                    padding-left: 1.25rem;
                    padding-right: 5rem;
                }
            }
            button {
                margin: 1.75rem 0;
                background-color: black;
                color: white;
                font-weight: 500;
                border: none;
                outline: none;
                display: flex;
                justify-content: center;
                align-items: center;
                text-transform: uppercase;
                cursor: pointer;
                padding: 1.15rem 1.75rem;
                transition: 0.12s linear;
                &:hover {
                    background-color: rgb(32, 32, 32);
                }
                .arrow-icon {
                    font-size: 1.325rem;
                    margin-left: 0.8rem;
                    color: rgb(195, 139, 33);
                }
            }
            .button-subheading {
                margin: 1rem 0;
                font-size: 0.9rem;
                line-height: 15px;
                width: 85%;
                span {
                    cursor: pointer;
                    color: rgb(255, 153, 0);
                    text-decoration: underline;
                }
            }
        }
    }
}

@media screen and (max-width: 1450px) {
    .orderVehicle-form {
        .form-container {
            width: 87.5%;
        }
    }
}

@media screen and (max-width: 1250px) {
    .orderVehicle-section {
        article {
            h2 {
                font-size: 5.2rem;
            }
            p {
                font-size: 1.35rem;
                width: 80%;
                line-height: calc(1.35rem * 1.3);
            }
        }
    }
    .orderVehicle-form {
        .form-container {
            width: 95%;
            video {
                width: 45%;
                height: 55vh;
            }
        }
    }
    .orderVehicle-form .form-container .form-info-wrapper .final-payment p {
        padding-right: 2.5rem;
    }
}

//! iPad Pro
@media screen and (orientation: portrait) and (max-width: 1050px) {
    .orderVehicle-section {
        margin-top: 15vh;
        article {
            h2 {
                font-size: 5rem;
                font-weight: 500;
            }
            p {
                font-size: 1.4rem;
                line-height: calc(1.4rem * 1.3);
            }
        }
        .orderVehicle-thumbnail {
            height: 45vh;
        }
    }
    .orderVehicle-form {
        h1 {
            font-weight: 500;
        }
        .form-container {
            width: 97.5%;
            video {
                width: 45%;
                height: 35vh;
            }
        }
    }
    .orderVehicle-form .form-container .form-info-wrapper .final-payment p {
        padding-right: 1rem;
    }
}

//! iPhone
@media screen and (max-width:430px) {
    .orderVehicle-section {
        margin-top: 15vh;
        article {
            h2 {
                font-size: 2.25rem;
                letter-spacing: 0.25px;
            }
            p {
                margin-top: 0.3rem;
                font-size: 17px;
                line-height: calc(17px * 1.3);
                width: 97.5%;
                text-wrap: pretty;
            }
        }
        .orderVehicle-thumbnail {
            height: 35vh;
        }
    }
    .orderVehicle-form {
        h1 {
            font-size: 3rem;
            letter-spacing: 0.25px;
        }
        .form-container {
            flex-direction: column;
            video {
                width: 97.5%;
                height: 35vh;
            }
            .form-info-wrapper {
                width: 97.5%;
                margin: 5vh auto 0;
                .form-info {
                    width: 100%;
                }
                .form-info-price {
                    .todayPay-price {
                        font-size: 1.75rem;
                        font-weight: 500;
                    }
                }
                .final-payment {
                    width: 100%;
                }
                button {
                    margin: 1.5rem auto;
                }
                .button-subheading {
                    width: 97.5%;
                    margin: 1rem auto;
                }
            }
        }
    }
    .orderVehicle-form .form-container .form-info-wrapper .final-payment p {
        padding-right: 1rem;
    }
}