footer {
    color: rgb(125, 125, 125);
    width: 85%;
    margin: 5vh auto 7.5vh;
    .footer-wrapper {
        border-top: 1px solid rgb(200, 200, 200);
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top: 1.25rem;
        margin-bottom: 1.5rem;
        .logo {
            user-select: none;
            cursor: pointer;
            height: 5vh;
            width: auto;
            object-fit: contain;
        }
        .footer-address {
            margin-top: 1.5rem;
            display: flex;
            flex-direction: column;
            font-size: 0.95rem;
            line-height: calc(0.95rem * 1.45);
        }
        .footer-mail {
            display: flex;
            align-items: center;
            justify-content: center;
            color: #000;
            font-size: 1.1rem;
            .mail-icon {
                margin-right: 0.85rem;
            }
        }
        .icons-wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
            .footer-icon {
                color: #000;
                font-size: 1.125rem;
                margin: 0 1.25rem;
                cursor: pointer;
            }
        }
    }
    ul {
        margin-top: 2rem;
        justify-content: flex-end;
        display: flex;
        flex-direction: row;
        li {
            list-style: none;
            padding: 0 1.75rem;
            line-height: calc(1.5rem * 1.4);
        }
    }
}

@media screen and (max-width: 1450px) {
    footer {
        width: 90%;
    }
}

@media screen and (max-width: 1240px) {
    footer {
        width: 95%;
        margin-bottom: 6vh;
    }
}

//! iPad Pro
@media screen and (orientation: portrait) and (max-width: 1050px) {
    footer {
        .footer-wrapper {
            .logo {
                height: 4vh;
            }
        }
    }
}

//! iPhone
@media screen and (max-width:430px) {
    footer {
        width: 95%;
        .footer-wrapper {
            .logo {
                height: 2.75vh;
            }
            .footer-address {
                letter-spacing: -0.25px;
                font-size: 12px;
            }
            .footer-mail {
                font-size: 14px;
                .mail-icon {
                    margin-right: 0.25rem;
                }
            }
            .icons-wrapper {
                flex-direction: column;
                .footer-icon {
                    font-size: 1.05rem;
                    margin: 0.45rem 0;
                    &:first-of-type {
                        display: none;
                    }
                }
            }
        }
        ul li {
            font-size: 13px;
            line-height: 1.75rem;
        }
    }
}