header {
    padding: 1.15rem 0;
    background-color: #F4F4F2;
    z-index: 100;
    top: 0;
    position: fixed;
    width: 100%;
    nav {
        width: 90%;
        margin: 0.75rem auto 0;
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex-direction: row;
        user-select: none;
        .logo {
            cursor: pointer;
            height: 5vh;
            width: auto;
        }
        .nav-menu {
            margin: 0.25rem 7.5rem 0;
            width: 33.3333%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            p {
                cursor: pointer;
                font-weight: 500;
                font-size: 0.875rem;
                text-transform: uppercase;
                transition: .15s ease-in-out;
                padding-bottom: 0.6rem;
                border-bottom: 2px solid transparent;
                &:hover {
                    transition: 0;
                    opacity: 0.75;
                    border-bottom: 2px solid orange;
                }
                &.active {
                    color: rgb(255, 150, 0);
                    border-bottom: 2px solid orange;
                }
            }
        }
        button {
            background-color: black;
            color: white;
            font-weight: 500;
            border: none;
            outline: none;
            display: flex;
            justify-content: center;
            align-items: center;
            text-transform: uppercase;
            cursor: pointer;
            padding: 0.825rem 1.5rem;
            transition: .15s ease-in-out;
            margin-bottom: 0.75rem;
            &:hover {
                background-color: rgb(30, 30, 30);
            }
            .arrow-icon {
                font-size: 1.65rem;
                margin-left: 0.75rem;
                color: rgb(195, 139, 33);
            }
        }
    }
}

//! laptopy
@media screen and (orientation: landscape) and (max-width: 1060px) {
    header {
        nav {
            width: 92.5%;
            .nav-menu {
                width: 50%;
                margin: 0.25rem 2.5rem 0;
            }
            button {
                font-size: 13px;
                font-weight: 400;
                padding: 0.7rem 1rem;
                width: fit-content;
                .arrow-icon {
                    font-size: 1.4rem;
                    margin-left: 0.5rem;
                }
            }
        }
    }
}

//! iPad Pro
@media screen and (orientation: portrait) and (max-width: 1050px) {
    header {
        nav {
            justify-content: space-between;
            width: 90%;
            .logo {
                height: 4vh;
            }
            .nav-menu {
                margin: 0.25rem 2.25rem 0;
                width: 52.5%;
                p {
                    font-size: 16px;
                }
            }
            button {
                font-size: 14px;
                font-weight: 400;
                padding: 0.75rem 1.1rem;
                width: fit-content;
                .arrow-icon {
                    font-size: 1.5rem;
                    margin-left: 0.5rem;
                }
            }
        }
    }
}

//! iPad Air
@media screen and (orientation: portrait) and (max-width: 840px) {
    header {
        nav {}
    }
}

//! iPhone
@media screen and (max-width:430px) {
    header {
        nav {
            width: 98%;
            .logo {
                height: 3vh;
                margin: 0 0.5rem;
            }
            .nav-menu {
                justify-content: space-around;
                margin: 0.25rem 0rem 0;
                width: 100%;
                p {
                    font-size: 14px;
                }
            }
            button {
                display: none;
            }
        }
    }
}